<template lang="pug">
.filter-table-wrap
    filter-component(
        v-if="!filtersHide"
        :filters="filters"
        :filterList="config.filterList"
        :buttonList="config.buttonList"
    )
    table-component(
        v-loading="loading"
        :data="tableList"
        :height="height"
        border
        :selection="selection"
        :resetIndex="resetIndex"
        :SerialNumName="SerialNumName"
        :tableColumns="config.tableColumns"
        :headerIsWrap="headerIsWrap"
        :pageNum="pagination.pageNum"
        :pageSize="pagination.pageSize"
        ref="sortTable"
    )
    el-pagination(
        :class="paginationFixed ? 'fixed' : ''"
        @size-change="handleChangePaginationSize"
        @current-change="handleChangePaginationNum"
        :page-sizes="[10, 20, 50, 100]"
        :current-page.sync="pagination.pageNum"
        :page-size="pagination.pageSize"
        v-show="!pageHide"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total")
</template>

<script>
import filterComponent from './filter-component/index.vue'
import tableComponent from './table-component'
export default {
    name: 'usmartFilterTable',
    props: {
        // ÂåÖÊã¨ÊêúÁ¥¢ÂàóË°®ÂíåtableColumnÁöÑÈÖçÁΩÆ
        config: {
            type: Object
        },
        // ÂàóË°®Êï∞ÊçÆ
        tableList: {
            type: Array
        },
        // filters ÂØπË±°ÊòØÊêúÁ¥¢ÁöÑÊù°‰ª∂ÂÄº v-modle
        filters: {
            type: Object
        },
        // ÊòØÂê¶ÈöêËóèÊêúÁ¥¢Ê°Ü
        filtersHide: {
            type: Boolean
        },
        // ÊòØÂê¶ÊúâÈÄâÊã©Ê°Ü
        selection: {
            type: Boolean
        },
        // ÊòØÂê¶ÈöêËóèÂàÜÈ°µÁªÑ‰ª∂Âêç
        pageHide: {
            type: Boolean
        },
        // Ëá™ÂÆö‰πâÂ∫èÂè∑
        resetIndex: {
            type: String
        },
        //Â§¥ÈÉ®Ê†áÈ¢òÊòØÂê¶Êç¢Ë°åÔºåtrue:Êç¢Ë°åÔºåfalse:‰∏çÊç¢Ôºå‰æãÂ¶ÇÔºöÊ†áÈ¢òone | Ê†áÈ¢òtwo
        headerIsWrap: {
            type: Boolean
        },
        // Table ÁöÑÈ´òÂ∫¶ÔºåÈªòËÆ§‰∏∫Ëá™Âä®È´òÂ∫¶„ÄÇTable ÁöÑÈ´òÂ∫¶‰ºöÂèóÊéß‰∫éÂ§ñÈÉ®Ê†∑Âºè
        height: {
            type: String
        },
        // Â∫èÂè∑ÁöÑÂêçÁß∞ÔºåÈªòËÆ§ÊòØÂ∫èÂè∑
        SerialNumName: {
            type: String
        },
        // ÊòØÂê¶Ê∑ªÂä†loading
        loading: {
            type: Boolean
        },
        // ÂàÜÈ°µÁªÑ‰ª∂
        pagination: {
            type: Object
        },
        paginationFixed: {
            type: Boolean,
            default: true
        }
    },
    components: {
        filterComponent,
        tableComponent
    },
    methods: {
        // ÂàáÊç¢ÂÖ®ÈÄâÁä∂ÊÄÅ ‰æõÁà∂Á∫ßË∞ÉÁî®Ôºå‰∏çÂèØÂà†Èô§
        toggleAllSelection() {
            this.$refs.sortTable.toggleAllSelection()
        },
        // pagenation methods
        handleChangePaginationSize(val) {
            console.log('handleChangePaginationSize==>', val)
            this.$emit('listenHandleChangePaginationSize', val)
        },
        handleChangePaginationNum(val) {
            this.$emit('listenHandleChangePaginationNum', val)
        }
    }
}
</script>

<style scoped lang="scss">
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
    .el-col-8 {
        width: 347px;
    }
    .el-col-6 {
        width: 300px;
        .el-select {
            width: auto;
        }
    }
    .el-col-16 {
        width: 530px;
    }
    .el-col-2 {
        width: auto;
    }
}
.el-col {
    border-radius: 4px;
    .range-input.el-input {
        width: 100px;
    }
}
.blue {
    color: #409eff;
}
.green {
    color: #70b603;
}
.yellow {
    color: #f59a23;
}
.red {
    color: #f52349;
}
.deepRed {
    color: #a60014;
}
.filter-table-wrap.fixed {
    padding-bottom: 50px;
}
.filter-table-wrap {
    padding: 15px;
    min-height: 500px;
    .search-box.el-form {
        display: flex;
        flex-wrap: wrap;
    }
    .extand {
        .el-form-item {
            width: 25%;
        }
    }
    .el-pagination {
        text-align: right;
    }
    .el-pagination.fixed {
        z-index: 10;
        background: #fff;
        position: fixed;
        right: 70px;
        bottom: 30px;
    }
}
</style>



